<template>
  <div>
    <h4 style="margin: 10px;">Episode Terbaru</h4>
    <v-row v-if="newepisodes !== null">
      <v-col v-for="newepisode in newepisodes" cols="12" md="6" :key="newepisode.id">
        <router-link :to="{ path: '/episode/'+encodeURI(newepisode.id) }">
          <v-card class="anime">
            <v-img :src="newepisode.cover" class="anime-image rounded" />
            <b class="anime-title">{{ newepisode.title }}</b>
            <div class="anime-details">
              <span class="item">Tipe : {{ newepisode.type }}</span>
              <span class="item">Rating : {{ newepisode.rate }}</span>
              <span class="item">Episode : {{ newepisode.episode }}</span>
            </div>
          </v-card>
        </router-link>
      </v-col>
    </v-row>

    <v-row v-if="newepisodes === null || loadNewepisodes === true">
      <v-col cols="12" md="6">
        <v-card class="anime">
          <v-skeleton-loader
            type="image"
            class="anime-image rounded"
          ></v-skeleton-loader>
          <v-skeleton-loader
            type="article"
            class="anime-title"
          ></v-skeleton-loader>
        </v-card>
      </v-col>

      <v-col cols="12" md="6">
        <v-card class="anime">
          <v-skeleton-loader
            type="image"
            class="anime-image rounded"
          ></v-skeleton-loader>
          <v-skeleton-loader
            type="article"
            class="anime-title"
          ></v-skeleton-loader>
        </v-card>
      </v-col>

      <v-col cols="12" md="6">
        <v-card class="anime">
          <v-skeleton-loader
            type="image"
            class="anime-image rounded"
          ></v-skeleton-loader>
          <v-skeleton-loader
            type="article"
            class="anime-title"
          ></v-skeleton-loader>
        </v-card>
      </v-col>

      <v-col cols="12" md="6">
        <v-card class="anime">
          <v-skeleton-loader
            type="image"
            class="anime-image rounded"
          ></v-skeleton-loader>
          <v-skeleton-loader
            type="article"
            class="anime-title"
          ></v-skeleton-loader>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<style scoped>
.anime {
  height: 160px;
}

.anime-image {
  width: 120px;
  height: 160px;
  float: left;
}

.anime-title {
  padding: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: block;
  font-weight: bold;
  font-size: 18px;
}

.anime-details {
  padding: 10px;
  display: block;
  margin-left: 120px;
}

.anime-details > .item {
  font-weight: 500;
  font-size: 14px;
  display: block;
}
</style>

<script>
import Vue from "vue";
import axios from "axios";

export default Vue.extend({
  name: "Index",
  data() {
    return {
      newepisodes: null,
      nextNewepisodes: '',
      loadNewepisodes: false,
    }
  },
  methods: {
    scroll() {
      window.onscroll = async () => {
        let bottomOfWindow = Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight === document.documentElement.offsetHeight;
        if (bottomOfWindow && this.nextNewepisodes !== '' && this.loadNewepisodes === false) {
          await this.loadNewepisode(this.nextNewepisodes);
        }
      }
    },
    async loadNewepisode(page = '') {
      if (this.newepisodes !== null) {
        this.loadNewepisodes = true;
        await axios.get("https://api.abpay.xyz/api/newepisode?api_key=b988b287-c0ff-408c-b941-9619f283db76&page="+page)
          .then(res => {
            this.nextNewepisodes = res.data.data.nextPage;
            for (let i = 0; i < res.data.data.data.length; i++) {
              this.newepisodes.push(res.data.data.data[i]);
            }
          }).catch(err => {
            console.log(err);
          });
        
        this.loadNewepisodes = false;
      }
    }
  },
  async created() {
    await axios.get("https://api.abpay.xyz/api/newepisode?api_key=b988b287-c0ff-408c-b941-9619f283db76")
      .then(res => {
        this.newepisodes = res.data.data.data;
        this.nextNewepisodes = res.data.data.nextPage;
      }).catch(err => {
        console.log(err);
      });
    
    this.scroll();
  },
});
</script>